<template>

  <div class="acco-item">
    <div class="acco-head">
      <button type="button" :aria-expanded="props.isActive" @click="selectAcco">
         <slot name="head"></slot>
      </button>
    </div>
    
    <div class="acco-body" :aria-hidden="!props.isActive" :style="{ height: bodyHeight }"> 
      <div class="acco-cont" ref="bodyAcco">
        <slot name="body"></slot>
      </div>
    </div>
  </div>

</template>
<script setup>
import { defineProps, defineEmits, ref, nextTick, watch } from 'vue'; 

const props = defineProps({
  isActive: Boolean,
});
const emit = defineEmits(['update:acco']);

const bodyHeight = ref('0px');
const bodyAcco = ref(null);

const selectAcco = async () => {
  emit('update:acco');

  await nextTick();
  if(props.isActive) {
    const _height = bodyAcco.value.scrollHeight + 'px';
    bodyHeight.value = _height;
  } else {
    bodyHeight.value = '0px';
  }
}

watch(() => props.isActive, async (newValue) => {  // watch에서 async 뒤에 있는 newValue는 감시하고 있는 값( 즉, 현재코드에선 props.isActive )의 새로운 값을 가리킨다.
  if(newValue) {
    await nextTick();
    bodyHeight.value = `${bodyAcco.value.scrollHeight}px`
  } else { 
    bodyHeight.value = '0px'
  }
})
</script>
